<template>
    <section>
        <div class="">
            <!-- <loader :is-visible="isLoading"></loader> -->
            <div class="row barrw" id="divconclusionheader">
                <div class="col-sm-12 col-md-12 col-lg-12">
                    <div class="heading-border theme-primary partition-full">
                        <h5 class="h5 text-white mt-2 ml-3">
                            <span class="mt-1">{{ $t('Service') }} </span>
                        </h5>
                    </div>
                </div>
            </div>

            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-2">
                <div class="row">
                    <div class="col-md-3">
                        <label>Work Type:</label>
                        <Field :name="'WorkType'" v-slot="{ errors }" :rules="{ 'required': true }">
                            <div class="dropdown">
                                <button class="btn btn-secondary no-hover custom-button" type="button"
                                    id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true"
                                    aria-expanded="false">
                                    {{ worktype_name }}
                                </button>
                            </div>
                        </Field>
                    </div>
                </div>
            </div>

            <template>
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-3">
                    <div class="row barrw" id="divconclusionheader">
                        <div class="col-sm-12 col-md-12 col-lg-12">
                            <div class="heading-border">
                                <h5 class="h5  mb-3 mt-2">
                                    <span> {{ $t('Service Item') }}</span>
                                </h5>
                            </div>
                        </div>
                    </div>
                    <div>
                        <table id="customers">
                            <tr>
                                <th><span>{{ $t('ServiceName') }}</span></th>
                                <th><span>{{ $t('Cost') }}</span></th>
                                <th><span>{{ $t('Markup') }}</span></th>
                                <th><span>{{ $t('UnitPrice') }}</span></th>
                                <th><span>{{ $t('ServiceDuration') }}</span></th>
                                <th><span>{{ $t('Action') }}</span></th>
                            </tr>
                            <tr>
                                <td><input type="text" id="fname" name="fname" v-model="serviceName" class="serviceText">
                                </td>
                                <td><input type="number" id="fname" name="fname" v-model="costWithSign"
                                        @input="onCostChange" class="serviceText">
                                </td>
                                <td><input type="number" id="fname" name="fname" v-model="markup" @input="onMarkupChange"
                                        class="serviceText">
                                </td>
                                <td><input type="number" id="fname" name="fname" v-model="unitPriceWithSign"
                                        @change="onUnitPriceChange" class="serviceText"></td>
                                <td><input type="number" id="fname" name="fname" v-model="serviceDuration"
                                        class="serviceText"></td>
                                <td><button class="delete-button">
                                        <i class="fa fa-trash"></i>
                                    </button></td>
                            </tr>
                        </table>
                    </div>
                </div>

            </template>
            <!-- v-if="is_publish == true" -->
            <template>
                <div id="accordion" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 activity-tab">
                    <div class="row barrw" id="divconclusionheader">
                        <div class="col-sm-12 col-md-12 col-lg-12">
                            <div class="heading-border card-header">
                                <a class="card-link" id="ancreplyExpandAllCollapseAll"
                                    @click="ancreplyExpandAllCollapseAll()" data-toggle="collapse" href="#collapseone"
                                    aria-expanded="true">
                                    <div class="conversation-date heading-border">
                                        <span style="font-weight: 400; !important"><i
                                                class="fa fa-angle-down rotate-icon pr-1" aria-hidden="true"></i>{{
                                                    $t('Publish Details') }}</span>
                                    </div>
                                </a>
                                <!-- <h5 class="h5  mb-3 mt-2">
                                    <span> {{ $t('Publish Details') }}</span>
                                </h5> -->
                            </div>
                        </div>
                    </div>
                </div>
                <div id="collapseone" class="collapse show" data-parent="#accordion" style="">
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                        <dynamic-form lang="en" :schema="FormSchema"></dynamic-form>
                    </div>
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                        <div class="row barrw availability" id="divconclusionheader">
                            <div class="col-sm-12 col-md-12 col-lg-12">
                                <div class="heading-border">
                                    <h5 class="h5  mb-3 mt-2">
                                        <span> {{ $t('Availability') }}</span>
                                    </h5>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="container-fluid">
                        <table class="table">
                            <tbody>
                                <tr v-for="(day, index) in weekdays" :key="index">
                                    <td>
                                        <div class="circle">{{ index + 1 }}</div>
                                    </td>
                                    <td>
                                        <input type="checkbox" :id="`weekday-checkbox-${index}`" class="larger-checkbox"
                                            v-model="day.checked" />
                                        <label class="weekCheck-time" :for="`weekday-checkbox-${index}`">{{ day.name
                                        }}</label>
                                    </td>
                                    <td class="col-md-3">
                                        <input type="checkbox" :id="`twenty-four-checkbox-${index}`" class="larger-checkbox"
                                            v-model="day.twentyFour" />
                                        <label class="weekCheck-time" :for="`twenty-four-checkbox-${index}`">24
                                            Hour</label>
                                    </td>
                                    <td v-if="day.timePicker">
                                        <div class="row">
                                            <div class="time-picker">
                                                <div class="time-container">
                                                    <label :for="`start-time-picker-${index}`">Start:</label>
                                                    <input type="time" :id="`start-time-picker-${index}`"
                                                        v-model="day.startTime" @input="updateStartTimeLabel(index)"
                                                        step="1800" class="custom-time-picker" />
                                                </div>
                                            </div>
                                            <div class="time-picker">
                                                <div class="time-container">
                                                    <label :for="`end-time-picker-${index}`">End:</label>
                                                    <input type="time" :id="`end-time-picker-${index}`"
                                                        v-model="day.endTime" @input="updateEndTimeLabel(index)" step="1800"
                                                        class="custom-time-picker" />
                                                </div>
                                            </div>
                                        </div>
                                    </td>
                                    <td v-else>
                                        Closed
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                        <div class="row barrw marketing" id="divconclusionheader">
                            <div class="col-sm-12 col-md-12 col-lg-12">
                                <div class="heading-border">
                                    <h5 class="h5  mb-3 mt-2">
                                        <span> {{ $t('Marketing Video') }}</span>
                                    </h5>
                                </div>
                            </div>
                        </div>
                        <div class="mb-3 mt-2">
                            <h6>
                                <span class="marketVideo">{{ $t('Having a marketing video that`ll help get people excited '
                                    + 'about your brand ? Include it
                                                                    here.Example: (https://www.youtube.com/embed/ly36kn0ug4k)')}}</span>
                            </h6>
                            <h6>{{ $t('Youtube:(You can upload maximum 5 links.)') }}</h6>
                        </div>
                        <div>
                            <div v-for="(textbox, index) in textboxes" :key="index" class="input-container mb-2">
                                <input type="text" class="link-input" v-model="textbox.value">
                                <i class="fa fa-trash delete-icon" v-on:click="removeTextBox(index)" v-if="index !== 0"></i>
                                <i v-else class="fa fa-plus-circle fa-lg add-icon" v-on:click="addNewTextBox()"
                                    style="margin-left: 10px;"></i>
                            </div>
                        </div>
                    </div>
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-2">
                        <div class="row barrw description" id="divconclusionheader">
                            <div class="col-sm-12 col-md-12 col-lg-12">
                                <div class="heading-border">
                                    <h5 class="h5  mb-3 mt-2">
                                        <span> {{ $t('Description') }}</span>
                                    </h5>
                                </div>
                            </div>
                        </div>
                        <div class="mb-3">
                            <dynamic-form lang="en" :schema="FormCkeditorSchema">
                                <template v-slot:tgslot-Description="{ data }">
                                    <ckeditor :editor="editor" @ready="onReady" v-model="data.fieldInfo.value">
                                    </ckeditor>
                                    <span name="Description" class="invalid-feedback d-block">{{ data.error }}</span>
                                    <!-- {{ data.fieldInfo }} -->
                                </template>
                            </dynamic-form>
                        </div>
                    </div>
                </div>

            </template>
            <div class="row flex-row-reverse">
                <div class="col-lg-6 text-right">
                    <a href="javascript:;" class="btn btn-success formbtn widthhalf mr-2" v-on:click="SubmitData()"
                        :title="$t('Save')">
                        <i class="fa fa-save pr-2"></i> {{ $t('Save') }}
                    </a>
                    <a href="javascript:;" class="btn btn-danger formbtn widthhalf" v-on:click="Close()"
                        :title="$t('Cancel')">
                        <i class="fa fa-close pr-2"></i> {{ $t('Cancel') }}
                    </a>

                </div>
            </div>
        </div>

    </section>
</template>


<script>
import DataServices from "@/services/DataService";
export default
    {
        data() {
            return {
                editor: this.CLASSIC_EDITOR,
                textboxes: [{ value: '' }],
                serviceName: '',
                cost: '',
                markup: '',
                unitprice: '',
                serviceDuration: '',
                data: '',
                unitPriceWithSign: '',
                costWithSign: '',
                worktype_id: null,
                worktype_name: null,
                servicetype_id: null,
                id: '',
                SortBy: '',
                SortExp: '',
                PageSize: "10",
                fileData: [],
                PageNumber: "1",
                is_publish: false,
                weekdays: [
                    { name: 'Monday', checked: false, twentyFour: false, startTime: '', endTime: '', timePicker: true },
                    { name: 'Tuesday', checked: false, twentyFour: false, startTime: '', endTime: '', timePicker: true },
                    { name: 'Wednesday', checked: false, twentyFour: false, startTime: '', endTime: '', timePicker: true },
                    { name: 'Thursday', checked: false, twentyFour: false, startTime: '', endTime: '', timePicker: true },
                    { name: 'Friday', checked: false, twentyFour: false, startTime: '', endTime: '', timePicker: true },
                    { name: 'Saturday', checked: false, twentyFour: false, startTime: '', endTime: '', timePicker: true },
                    { name: 'Sunday', checked: false, twentyFour: false, startTime: '', endTime: '', timePicker: true }
                ],
                FormSchema: [
                    {
                        layoutType: "four",
                        Data: [
                            {
                                astype: "NumericField",
                                label: this.$t('Service Duration(HH:MM)'),
                                name: "Service Duration",
                                value: "",
                                validationRules: { "required": false, max: 100 },
                            },
                            {
                                astype: "SelectField",
                                label: this.$t('Numbers of booking allowed per slot'),
                                name: "",
                                value: "",
                                config: {
                                    options: [{ value: "1", name: "1" }, { value: "2", name: "2" }, { value: "3", name: "3" }]
                                },
                                validationRules: { "required": false, max: 100 },
                            },
                            {
                                astype: "toggleField",
                                label: this.$t('Raise Request'),
                                name: "Advance Payment",
                                value: "",
                                placeholder: "",
                                validationRules: "",
                            },
                            {
                                astype: "toggleField",
                                label: this.$t('Advance Payment'),
                                name: "Advance Payment",
                                value: "",
                                placeholder: "",
                                validationRules: "",
                            },
                        ],
                    },
                    {
                        layoutType: "single",
                        Data: [
                            {
                                astype: "FileField",
                                showLabel: false,
                                name: "ListAttachment",
                                value: "",
                                placeholder: "",
                                validationRules: "",
                                FieldIndex: 0,
                                config: {
                                    maxSize: 5,
                                    accept: [
                                        {
                                            fileType: 'png',
                                            iconClass: 'fa-file-image-o'
                                        },
                                        {
                                            fileType: 'jpg',
                                            iconClass: 'fa-file-image-o'
                                        },
                                        {
                                            fileType: 'pdf',
                                            iconClass: 'fa-file-archive-o'
                                        }
                                    ],
                                    showSelectedFiles: [],
                                    onDeleteSelectedFiles: this.onDeleteSelectedFiles,
                                    onChange: this.GetBase64String,
                                    showAddIcon: false,
                                    onAddButtonClick: "",
                                    multiple: false
                                },
                            }
                        ]
                    }
                ],
                FormCkeditorSchema: [
                    {
                        layoutType: "single",
                        Data: [
                            {
                                astype: "SlotField",
                                label: "",
                                name: "Description",
                                value: "",
                                placeholder: "",
                                config: {
                                    options: [],

                                },
                                settings: {
                                    isInSlot: true
                                },
                                visibility: true,
                                validationRules: { "required": false, max: 5000 },
                            },
                        ]
                    },
                ],
            }
        },
        created: async function () {
            debugger
            var vm = this;
            vm.id = this.$route.params.id;
            vm.FetchRecords(vm.id);
        },
        methods: {
            onCostChange: function () {
                let cost = parseFloat(this.costWithSign);
                let markup = parseFloat(this.markup);
                if (!isNaN(cost) && !isNaN(markup)) {
                    this.unitPriceWithSign = this.calculateUnitPrice(cost, markup);
                }
            },
            onUnitPriceChange: function () {
                let unitPrice = parseFloat(this.unitPriceWithSign);
                let cost = parseFloat(this.costWithSign);
                if (!isNaN(unitPrice) && !isNaN(cost)) {
                    this.markup = this.calculateMarkupPercentage(cost, unitPrice);
                }
            },
            onMarkupChange: function () {
                let markup = parseFloat(this.markup);
                let cost = parseFloat(this.costWithSign);
                if (!isNaN(markup) && !isNaN(cost)) {
                    this.unitPriceWithSign = this.calculateUnitPrice(cost, markup);
                }
            },
            calculateUnitPrice: function (cost, markup) {
                return (cost + (cost * markup / 100)).toFixed(2);
            },
            calculateMarkupPercentage: function (cost, unitPrice) {
                return (((unitPrice - cost) / cost) * 100).toFixed(2);
            },
            onReady(editor) {
                editor.ui.getEditableElement().parentElement.insertBefore(
                    editor.ui.view.toolbar.element,
                    editor.ui.getEditableElement()
                );
            },
            Close: function () {
                this.$router.push({ name: "ServiceListing" });
            },
            FetchRecords: async function (serviceType_id) {
                debugger;
                var vueObj = this;
                var srchCondition = "";
                var url = `serviceTypeId=${serviceType_id}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pagesize=${vueObj.PageSize}&pagenumber=${vueObj.PageNumber}`;
                if (!this.isBlank(this.searchCondition)) {

                    srchCondition = srchCondition + this.searchCondition;
                }
                if (!this.isBlank(this.conditionForInstantSearch)) {
                    srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
                }
                if (srchCondition != "") {
                    url = url + "&search=" + srchCondition;
                }
                await DataServices.GetServiceListingById(url).then(function (response) {
                    debugger;
                    if (response.data != "" && response.data != null) {
                        if (response.data.result.data.length > 0) {
                            vueObj.ServiceListing = response.data.result.data;
                            vueObj.worktype_id = vueObj.ServiceListing[0].worktype_id;
                            vueObj.servicetype_id = vueObj.ServiceListing[0].service_type_id;
                            vueObj.is_publish = vueObj.ServiceListing[0].is_publish;
                            vueObj.worktype_name = vueObj.ServiceListing[0].worktype_name;
                            vueObj.FormSchema[0].Data[0].value = vueObj.ServiceListing[0].service_duration;
                            vueObj.serviceName = vueObj.ServiceListing[0].service_name;
                            vueObj.cost = vueObj.ServiceListing[0].cost;
                            vueObj.markup = vueObj.ServiceListing[0].markup;
                            vueObj.unitprice = vueObj.ServiceListing[0].unit_price;
                            vueObj.serviceDuration = vueObj.ServiceListing[0].service_duration;
                            vueObj.unitPriceWithSign = vueObj.unitprice;
                            vueObj.FormCkeditorSchema[0].Data[0].value = vueObj.ServiceListing[0].description;
                            vueObj.costWithSign = vueObj.cost;
                            vueObj.FormSchema[0].Data[1].value = vueObj.ServiceListing[0].total_bookings;
                            vueObj.FormSchema[0].Data[2].value = vueObj.ServiceListing[0].raise_request;
                            vueObj.FormSchema[0].Data[3].value = vueObj.ServiceListing[0].advance_payment;
                            var attachmentData = {
                                attachmentName: vueObj.ServiceListing[0].fileName, // Replace with your attachment name
                                attachmentPath: vueObj.ServiceListing[0].filePath  // Replace with your attachment path
                            };
                            if (attachmentData.attachmentName != null && attachmentData.attachmentPath != null) {
                                vueObj.FormSchema[1].Data[0].config.showSelectedFiles.push(attachmentData);
                            }
                            var weekdaysData = vueObj.ServiceListing[0].availability_data;
                            weekdaysData = weekdaysData.replace('<availability_data>', '').replace('</availability_data>', '');
                            vueObj.weekdays = JSON.parse(weekdaysData);
                            var video_links = vueObj.ServiceListing[0].video_links;
                            var regex = /<video_link>(.*?)<\/video_link>/g;
                            var extractedUrls = [];
                            var match;
                            while ((match = regex.exec(video_links)) !== null) {
                                extractedUrls.push(match[1].replace(/amp;/g, ''));
                            }


                            while (vueObj.textboxes.length < extractedUrls.length) {
                                vueObj.addNewTextBox();
                            }

                            extractedUrls.forEach(function (link, index) {
                                if (index < vueObj.textboxes.length) {
                                    vueObj.textboxes[index].value = link;
                                }
                            });

                            // vueObj.weekdays.forEach(function (day) {
                            //     if (!day.checked && !day.timePicker) {
                            //         day.disabled = true;
                            //         day.closed = true;
                            //     }
                            // });
                            vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            vueObj.noRecord = false;
                        } else {
                            vueObj.noRecord = true;
                            vueObj.TotalRecords = 0
                            vueObj.ServiceListing = [];
                        }
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0
                        vueObj.ServiceListing = [];
                    }
                    setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                })
            },
            onDeleteSelectedFiles: function (item) {
                debugger
                this.$delete(this.FormSchema[1].Data[0].config, 'showSelectedFiles');
            },
            addNewTextBox: function () {
                if (this.textboxes.length < 5) {
                    this.textboxes.push({ value: '' });
                }
                else {
                    console.log('Maximum limit reached for adding textboxes.');
                }
            },
            removeTextBox: function (index) {
                var vm = this;
                vm.textboxes.splice(index, 1);
            },
            SubmitData: async function () {
                var vm = this;
                var availabilityData = [];
                vm.weekdays.forEach(function (day, index) {
                    var availabilityObj = {
                        name: day.name,
                        checked: day.checked,
                        twentyFour: day.twentyFour,
                        startTime: day.startTime,
                        endTime: day.endTime,
                        timePicker: day.timePicker
                    };
                    availabilityData.push(availabilityObj);
                });

                var unitPrice = parseFloat(vm.unitPriceWithSign);
                var cost = parseFloat(vm.costWithSign);
                const textBoxValues = this.textboxes.map(textbox => textbox.value);

                var postString = {
                    worktype_id: vm.worktype_id,
                    servicetype_id: vm.servicetype_id,
                    servicetype_name: vm.serviceName,
                    cost: cost,
                    markup: vm.markup,
                    unitprice: unitPrice,
                    Service_duration: vm.FormSchema[0].Data[0].value,
                    Bookings: vm.FormSchema[0].Data[1].value,
                    advancePayment: vm.FormSchema[0].Data[2].value == true ? 1 : 0,
                    raiseRequest: vm.FormSchema[0].Data[3].value == true ? 1 : 0,
                    description: vm.FormCkeditorSchema[0].Data[0].value,
                    AvailabilityData: availabilityData,
                    FileData: vm.fileData,
                    links: textBoxValues,
                    type: 'EDIT'
                };
                debugger;
                var objectPostString = JSON.stringify(postString);
                await DataServices.SavePublishDetail(objectPostString).then(function (response) {
                    if (response.data.result != "" && response.data.result != null) {
                        if (response.data.result == 'UPDATE') {
                            vm.$router.push({ name: 'ServiceListing' });
                            vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Service')]), "success", true, vm.$t("Alert"));
                        }
                        else if (response.data.result == 'ADD') {
                            vm.$router.push({ name: 'ServiceListing' });
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Service')]), "success", true, vm.$t("Alert"));
                        }
                        else {
                            vm.ShowAlert(vm.$t('ErrorMessage', [vm.$t('Service')]), "failure", true, vm.$t("Alert"));
                        }
                    }
                });

            },
            GetBase64String: function (evt) {
                try {
                    // debugger;
                    var vm = this;
                    //alert(vm.apiurl);
                    vm.fileData = [];

                    var f = evt.target.files[0]; // FileList object
                    var reader = new FileReader();
                    // Closure to capture the file information.
                    reader.onload = (function (theFile) {
                        debugger;
                        return function (e) {
                            var binaryData = e.target.result;
                            //Converting Binary Data to base 64
                            var base64StringEncoded = window.btoa(binaryData);
                            //showing file converted to base64

                            var fileName = f.name;
                            vm.fileName = fileName;
                            var n = fileName.lastIndexOf(".");
                            var extensionName = fileName.substring(n + 1, fileName.length);

                            vm.fileExt += "." + extensionName;
                            var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                            var fileSize = f.size;
                            vm.fileSize = fileSize;
                            // debugger;
                            // var ggg = vm.configData;
                            var fileObj = {
                                attachement_mime: attachemnetMime,
                                fileName: fileName,
                                id: vm.id,
                                moduleName: vm.modulename,
                                subModuleCode: vm.submodulecode,
                                base64String: base64StringEncoded,
                                fileSize: fileSize
                            };

                            vm.fileData.push(fileObj);
                        };
                    })(f);
                    // Read in the image file as a data URL.
                    reader.readAsBinaryString(f);
                }
                catch (ex) {
                }
            },
        }

    }
</script>


<style>
.design-container {
    display: flex;
    align-items: center;
    margin-left: 13px;
    margin-top: 8px;
}

.circle {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #e2e7ec;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 50px;
}

input[type="checkbox"] {
    margin-right: 15px;
}

.larger-checkbox {
    transform: scale(1.5);
    /* Increase the size of the checkboxes */
}

.weekCheck-time {
    font-size: 16px;
    font-family: inherit;
}

.time-picker {
    display: inline-block;
    width: 200px;
    margin-right: 5px;
}

.time-container {
    border: 1px solid #ccc;
    padding: 10px;
    border-radius: 10px;
    /* Adjust border radius as needed */
}

.time-container label {
    display: block;
    margin-bottom: 5px;
}

.time-container input[type="time"] {
    width: 100%;
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 3px;
}

.custom-time-picker {
    height: 40px;
    /* Adjust the height as needed */
}

